import * as S from "../styles";

function Resistances({resistances}) {
  return(
      <S.Table width="53%">
        <caption> </caption>
        <thead>
        <tr>
          <th>
            <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/.webp`} alt="" title="" /></S.Icon>
          </th>
          <th>
            <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/.webp`} alt="" title="" /></S.Icon>
          </th>
          <th>
            <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/.webp`} alt="" title="" /></S.Icon>
          </th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>{resistances.slash}</td>
          <td>{resistances.pierce}</td>
          <td>{resistances.blunt}</td>
        </tr>
        </tbody>
      </S.Table>
  )
}

export default Resistances;
